import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CheckResult } from 'src/app/shared/interfaces/interfaces';

@Component({
  selector: 'app-checkimg-result',
  templateUrl: './checkimg-result.component.html',
  styleUrls: ['./checkimg-result.component.css']
})
export class CheckimgResultComponent implements OnInit {

  @Input('result') inResult: CheckResult
  @Output() onClose: EventEmitter<any> = new EventEmitter()

  result: CheckResult
  compare = {
    1: '     ',
    2: '       RGB',
    3: ' 2D   (QR)   ',
    4: ' 20-30   ',
    5: '  '
  }

  constructor() { }

  ngOnInit() {
    console.log(this.inResult)
    this.result = {result: this.inResult.result ? ' .' : ' .', info: this.inResult.info == 'none' ? '' : this.inResult.info, method: this.compare[this.inResult.type]}
  }

  close() {
    this.onClose.emit(true)
  }

}
